import Machines_D_0 from '../Machines_D/Machines_D_0'
import Machines_D_1 from '../Machines_D/Machines_D_1'
import Machines_D_2 from '../Machines_D/Machines_D_2'
import Products_D_1 from '../Products_D/Products_D_1'
import Products_B_0 from '../Products_B/Products_B_0'
import Products_A_5 from '../Products_A/Products_A_5'
import Options from '../Options'

export default function Products_E_1 (output = 60, machine = 0, options) {
  options = options || Options
  let factory
  switch (options.Producer) {
    case 3:
      if (machine > 0) {
        output = machine * (60 / 2)
      } else {
        machine = output / (60 / 2)
      }
      factory = Machines_D_2(machine.toFixed(1))
      break
    case 2:
      if (machine > 0) {
        output = machine * (60 / 3)
      } else {
        machine = output / (60 / 3)
      }
      factory = Machines_D_1(machine.toFixed(1))
      break
    case 1:
    default:
      if (machine > 0) {
        output = machine * (60 / 4)
      } else {
        machine = output / (60 / 4)
      }
      factory = Machines_D_0(machine.toFixed(1))
  }
  return {
    name: '',
    icon: '/static/img/products/e1.png',
    machine: factory,
    children: [
      Products_D_1(output * 2, 0, options),
      Products_B_0(output * 3, 0, options),
      Products_A_5(output * 1, 0, options)
    ],
    output
  }
}



