import Machines_D_0 from '../Machines_D/Machines_D_0'
import Machines_D_1 from '../Machines_D/Machines_D_1'
import Machines_D_2 from '../Machines_D/Machines_D_2'
import Products_B_4 from '../Products_B/Products_B_4'
import Products_A_3 from '../Products_A/Products_A_3'
import Materials_A_7 from '../Materials_A/Materials_A_7'
import Options from '../Options'

export default function Products_C_3 (output = 60, machine = 0, options) {
  options = options || Options
  let factory
  switch (options.Producer) {
    case 3:
      if (machine > 0) {
        output = machine * (60 / 1.7)
      } else {
        machine = output / (60 / 1.7)
      }
      factory = Machines_D_2(machine.toFixed(1))
      break
    case 2:
      if (machine > 0) {
        output = machine * (60 / 2.5)
      } else {
        machine = output / (60 / 2.5)
      }
      factory = Machines_D_1(machine.toFixed(1))
      break
    case 1:
    default:
      if (machine > 0) {
        output = machine * (60 / 3.3)
      } else {
        machine = output / (60 / 3.3)
      }
      factory = Machines_D_0(machine.toFixed(1))
  }
  return {
    name: '',
    icon: '/static/img/products/c3.png',
    machine: factory,
    children: [
      Products_B_4(output, 0, options),
      Products_A_3(output, 0, options),
      Materials_A_7(output, 0, options)
    ],
    output
  }
}



